import { useEffect, useState } from "react";
import axios from "axios";
import { Buffer } from "buffer";
import parse from "html-react-parser";


export default function App() {
  const [content, setContent] = useState("");
  const [html, setHtml] = useState("");
  try {
    useEffect(() => {
      axios.get("https://api.github.com/repos/isreeks/Web101-HTML-CSS-JS-JSDOM/contents/CSS-basics/colors.md", {
        headers: {
          "Authorization": "Bearer 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"
        }
      })
        .then((res) => res.data)
        .then((data) => setContent(data.content));
    }, []);

  } catch (error) {
    console.log(error);

  }
  let bufferObj = Buffer.from(content, "base64");
  let decodedString = bufferObj.toString("utf8");





  // async function render(markdown) {
  //   return (await fetch('https://api.github.com/markdown', {
  //     method: 'POST',
  //     headers: {
  //       "Access-Control-Allow-Origin": "*",
  //       "Access-Control-Allow-Headers":"X-Requested-With",
  //       'Content-Type': 'application/json',
  //       "Authorization": "Bearer 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"
  //     },
  //     body: JSON.stringify({ 'mode': 'markdown', 'text': markdown })
  //   }))
  // }


  let axiosConfig = {
    headers: {
      'Content-Type': 'application/json',
      'Authorization': 'Bearer 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'
    },
  };

  useEffect(() => {

    async function render() {
      return (await axios.post('https://api.github.com/markdown', { text: decodedString }, axiosConfig))
    }
  
    var rendered = render(decodedString)
  
    rendered.then(function (result) {
      setHtml(result)
    })
  

  }, [decodedString])
  





  return (
    <div className="App">
      <div className="card">
        {parse(`${html.data}`)}
      </div>
    </div>
  );
}